.product {
    width: 200px;
    margin: 50px;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    box-sizing: border-box;
    padding: 20px;
    border-radius: 10px;

    img {
        width: 150px;
    }
}